.pricing-screen {
  height: 100vh;
  display: block !important;
  /*align-content: center;*/
  /*align-items: center;*/
  text-align: center;
  padding: 0 !important;
  margin: 0;
  background-image: url('../bck.png');
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.pricing-screen .row {
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
}

.pricing-screen h1 {
  margin-top: 30px !important;
}

.pricing-screen h2 {
  margin-top: 30px !important;
  color: #949494;
}

.pricing-screen .pricing {
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 20px;
}

.pricing-screen .pricing .card {
  padding: 20px;
  border: 1px solid #364659;
  margin-bottom: 30px;
  text-align: center;
}

.pricing-screen .pricing .card h3 {
  font-weight: normal;
}

.pricing-screen .pricing .card .best-value {
  color: #2569ff;
  padding-bottom: 8px;
  font-weight: 500;
  height: 30px;
}

.pricing-screen .pricing .price-block button {
  margin-top: 15px;
  max-width: 180px;
  font-weight: 500;
  margin-left: auto;
  margin-right: auto;
}

.pricing-screen .pricing .popular {
  background-color: aliceblue;
  border: 1px solid blue;
}

.pricing-screen .pricing .price {
  font-size: 3em;
  font-weight: 200;
}

.pricing-screen .pricing hr {
  margin-bottom: 0px;
}

.pricing-screen .pricing h3 {
  padding-top: 16px;
  padding-bottom: 8px;
  font-weight: bolder;

}

.pricing-screen .pricing .feature-point {
  min-height: 40px;
  margin-top: auto;
  margin-bottom: auto;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
}

.pricing-screen .pricing .highlight {
  background-color: #f4f4f4;
}

.pricing-screen .pricing .hide {
  color: transparent;
}

.pricing-screen .pricing .tx-left {
  justify-content: left;
}
